<template>
    <div class="container">
        <button @click="switch_list">{{ nextStatus }}</button>
        <div class="painter-container" v-show="nextStatus == '关闭'">
            <button
                v-for="(item, index) in painters"
                :key="index"
                :class="
                    'painter ' +
                    item.color +
                    (painterCode == item.code ? ' show' : '')
                "
                @click="set_code(item.code)"
            >
                {{ item.name }}
            </button>
        </div>
    </div>
</template>

<script>
export default {
    name: "WordPainter",

    data() {
        return {
            painterCode: 0,
            painters: [
                {
                    color: "red",
                    name: "红",
                    code: 1,
                },
                {
                    color: "blue",
                    name: "蓝",
                    code: 2,
                },
                {
                    color: "green",
                    name: "绿",
                    code: 3,
                },
                {
                    color: "white",
                    name: "清除全部",
                    code: 0,
                },
            ],
            nextStatus: "关闭",
        };
    },

    props: ["set_painter_code", "paint_clear"],

    methods: {
        set_code(val) {
            if (val == 0) this.paint_clear();
            if (this.painterCode == val) val = 0;
            this.painterCode = val;
            this.set_painter_code(val);
        },
        switch_list() {
            if (this.nextStatus == "关闭") this.nextStatus = "打开";
            else this.nextStatus = "关闭";
        },
    },
};
</script>

<style scoped>
.container {
    position: fixed;
    right: 10px;
    display: flex;
    flex-direction: column;
    top: 20%;
    width: 70px;
}

.painter-container {
    position: relative;
    display: flex;
    flex-direction: column;
}

.painter {
    height: 70px;
    width: 70px;
    font-size: 20px;
    border: solid 1px;
    opacity: 0.3;
}

.show {
    opacity: 1;
}

.red {
    color: white;
    background-color: red;
}
.blue {
    color: white;
    background-color: blue;
}
.green {
    color: white;
    background-color: green;
}
.white {
    background-color: white;
}
</style>